

import { WhiteSpace } from 'antd-mobile';
export default class Shadowchoice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            msg1: [
                '送女友',
                '送男友',
                '送朋友',
                '送长辈',
            ],
            msg2: [
                '爱情鲜花',
                '生日鲜花',
                '探病慰问',
                '商务鲜花',
                '祝福庆贺',
                '婚庆鲜花',
                '道歉鲜花',
                '哀思鲜花',
            ],
            msg3: [
                '玫瑰',
                '百合',
                '康乃馨',
                '向日葵',
                '扶郎',
                '绣球',
                '郁金香',
            ],
            msg4: [
                '传情花束',
                '创意花盒',
                '瓶插花',
            ],
        }
    }
    componentDidMount() {
        var clearone = document.querySelectorAll('.clearone');
        for (var i = 0; i < clearone.length; i++) {
            clearone[i].onclick = (e) => {
                e.stopPropagation();
                var clearone = document.querySelectorAll('.clearone');
                for (var i = 0; i < clearone.length; i++) {
                    clearone[i].style.color = 'black';
                    clearone[i].style.border = 'none';
                }
                console.log(e.srcElement.innerText);
                this.props.getchildparams(e.srcElement.innerText);
                e.srcElement.style.color = 'orange';
                e.srcElement.style.border = '1px solid orange';

            }

        }
    }
    render() {
        const {
            msg1,
            msg2,
            msg3,
            msg4,
        } = this.state;
        return (
            <div className="choicemsg">
                {/* --------------------------------------------------- */}
                <div>
                    <h2 style={{ fontSize: '22px', marginLeft: '10px' }}>人群</h2>
                    <WhiteSpace size="lg" />
                    <div style={{ display: "flex", flexWrap: 'wrap', justifyContent: 'space-around' }}>
                        {
                            msg1.map((m, i) => {
                                return (
                                    <div className="clearone" key={i} style={{
                                        width: "20%",
                                        lineHeight: "30px",
                                        margin: '10px 10px',
                                        background: '#f7f9fa',
                                        boxSizing: 'border-box',
                                        textAlign: 'center'
                                    }}>{m}</div>
                                )
                            })
                        }
                    </div>
                </div>
                {/* --------------------------------------------------------------- */}
                <div>
                    <h2 style={{ fontSize: '22px', marginLeft: '10px' }}>场合</h2>
                    <WhiteSpace size="lg" />
                    <div style={{ display: "flex", flexWrap: 'wrap', justifyContent: 'space-around' }}>
                        {
                            msg2.map((m, i) => {
                                return (
                                    <div className="clearone" key={i} style={{
                                        width: "20%",
                                        lineHeight: "30px",
                                        margin: '10px 10px',
                                        background: '#f7f9fa',
                                        boxSizing: 'border-box',
                                        textAlign: 'center'
                                    }}>{m}</div>
                                )
                            })
                        }
                    </div>
                </div>
                {/* --------------------------------------------------------------- */}
                <div>
                    <h2 style={{ fontSize: '22px', marginLeft: '10px' }}>花材</h2>
                    <WhiteSpace size="lg" />
                    <div style={{ display: "flex", flexWrap: 'wrap', justifyContent: 'space-around' }}>
                        {
                            msg3.map((m, i) => {
                                return (
                                    <div className="clearone" key={i} style={{
                                        width: "20%",
                                        lineHeight: "30px",
                                        margin: '10px 10px',
                                        background: '#f7f9fa',
                                        boxSizing: 'border-box',
                                        textAlign: 'center'
                                    }}>{m}</div>
                                )
                            })
                        }
                    </div>
                </div>
                {/* --------------------------------------------------------------- */}
                <div>
                    <h2 style={{ fontSize: '22px', marginLeft: '10px' }}>类别</h2>
                    <WhiteSpace size="lg" />
                    <div style={{ display: "flex", flexWrap: 'wrap', justifyContent: 'space-around' }}>
                        {
                            msg4.map((m, i) => {
                                return (
                                    <div className="clearone" key={i} style={{
                                        width: "20%",
                                        lineHeight: "30px",
                                        margin: '10px 10px',
                                        background: '#f7f9fa',
                                        boxSizing: 'border-box',
                                        textAlign: 'center'
                                    }}>{m}</div>
                                )
                            })
                        }
                    </div>
                </div>
                <div className="clearall" style={{ lineHeight: "60px", textAlign: 'center', background: '#e9ecf0', fontSize: '18px' }}>
                    清除选择
                </div>
            </div>
        )
    }
}