


import preventdefa from './preventdefa';
import { axios } from '&';
import './index.scss';
import { history } from '&/history';
import { NavBar, Icon, Popover } from 'antd-mobile';
import { Flex, WhiteSpace } from 'antd-mobile';
import Shadowchoice from '../shadowchoice';
const Item = Popover.Item;
export default class Findmore extends Component {
    constructor() {
        super();
        this.state = {
            paramsg: '爱情鲜花',
            querytimes: 0,
            idrank: [],
            img2: [

            ],
            text2: [

            ],
        }
    }
    //-----------------------------------------------------------------------------
    getchildparams = (res) => {
        var shadowchoice = document.getElementsByClassName('shadowchoice')[0];
        shadowchoice.style.display = "none";
        this.setState({
            paramsg: res
        })
        axios.get(
            '/vue/findmore',
            {
                params: {
                    limit: 8,
                    paramsg: this.state.paramsg,
                    querytimes: this.state.querytimes
                }
            }
        ).then((res) => {
            console.log(res.data.result);
            var rollIn = document.querySelectorAll('.rollIn');
            rollIn.forEach((item) => {
                item.className == "wobble";
                console.log(item.className);
            })
            this.state.img2 = [];
            this.state.text2 = [];
            this.state.idrank = [];
            for (var i = 0; i < res.data.result.length; i++) {
                var arr = [];
                console.log(res.data.result[i]);
                this.state.idrank.push(res.data.result[i]._id)
                this.state.img2.push(res.data.result[i].msgarr[0]);
                arr.push(res.data.result[i].msgarr[1]);
                arr.push(res.data.result[i].msgarr[2]);
                arr.push(res.data.result[i].msgarr[3]);
                this.state.text2.push(arr);
            }
            this.setState({
                img2: this.state.img2,
                text2: this.state.text2,
                idrank: this.state.idrank,
            })

            sessionStorage.findmoredatamsg = JSON.stringify(res.data.result);
        })
    }
    gotochoice = () => {
        var shadowchoice = document.getElementsByClassName('shadowchoice')[0];
        shadowchoice.style.display = "block";
        document.body.addEventListener('touchmove', preventdefa, { passive: false });  //passive 参数不能省略，用来兼容ios和android
    }


    // -------------------------------------------------------------------------------------
    componentDidMount() {
        var shadowchoice = document.getElementsByClassName('shadowchoice')[0];
        shadowchoice.onclick = function () {
            this.style.display = "none";
            document.body.removeEventListener('touchmove', preventdefa, { passive: false });
        }
        var boxdiv = document.querySelector('.boxdiv');
        var choiceone = document.querySelectorAll('.choiceone');
        boxdiv.addEventListener('click', (e) => {
            choiceone[0].style.color = 'black';
            choiceone[1].style.color = 'black';
            choiceone[2].style.color = 'black';
            choiceone[3].style.color = 'black';
            var e = e || whidow.event;
            var element = e.srcElement || e.which;
            element.style.color = '#ff734c';
        }, false);
        var placeholderdiv = document.querySelector('.placeholderdiv');
        placeholderdiv.addEventListener('click', (e) => {
            var placeholder = document.querySelectorAll('.placeholder');
            placeholder[0].style.color = 'black';
            placeholder[1].style.color = 'black';
            placeholder[2].style.color = 'black';
            placeholder[3].style.color = 'black';
            placeholder[4].style.color = 'black';
            var e = e || whidow.event;
            var element = e.srcElement || e.which;
            element.style.color = '#ff734c';
        }, true)
        if (sessionStorage.findmoredatamsg == undefined) {
            axios.get(
                '/vue/findmore',
                {
                    params: {
                        limit: 8,
                        paramsg: this.state.paramsg,
                        querytimes: this.state.querytimes
                    }
                }
            ).then((res) => {
                console.log(res.data.result);
                sessionStorage.findmoredatamsg = JSON.stringify(res.data.result)
                for (var i = 0; i < res.data.result.length; i++) {
                    var arr = [];
                    this.state.img2.push(res.data.result[i].msgarr[0]);
                    this.state.idrank.push(res.data.result[i]._id);
                    arr.push(res.data.result[i].msgarr[1]);
                    arr.push(res.data.result[i].msgarr[2]);
                    arr.push(res.data.result[i].msgarr[3]);
                    this.state.text2.push(arr);
                }
                this.setState({
                    img2: this.state.img2,
                    text2: this.state.text2,
                    idrank: this.state.idrank,
                })
            })
        } else {
            var result = JSON.parse(sessionStorage.findmoredatamsg)
            for (var i = 0; i < result.length; i++) {
                var arr = [];
                this.state.img2.push(result[i].msgarr[0]);
                this.state.idrank.push(result[i]._id);
                arr.push(result[i].msgarr[1]);
                arr.push(result[i].msgarr[2]);
                arr.push(result[i].msgarr[3]);
                this.state.text2.push(arr);
            }
            this.setState({
                img2: this.state.img2,
                text2: this.state.text2,
                idrank: this.state.idrank,
            })
        }

        // -----------------------------------------------------------------------------------
        window.onscroll = () => {
            let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
            let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
            if (Math.abs(scrollTop + clientHeight - scrollHeight) <= 1) {
                console.log("到底部了");
                this.setState({
                    querytimes: ++this.state.querytimes
                })
                setTimeout(() => {
                    axios.get(
                        '/vue/findmore',
                        {
                            params: {
                                limit: 8,
                                paramsg: this.state.paramsg,
                                querytimes: this.state.querytimes
                            }
                        }
                    ).then((res) => {
                        console.log(res.data.result);
                        for (var i = 0; i < res.data.result.length; i++) {
                            var arr = [];
                            console.log(res.data.result[i]);
                            this.state.idrank.push(res.data.result[i]._id);
                            this.state.img2.push(res.data.result[i].msgarr[0]);
                            arr.push(res.data.result[i].msgarr[1]);
                            arr.push(res.data.result[i].msgarr[2]);
                            arr.push(res.data.result[i].msgarr[3]);
                            this.state.text2.push(arr);
                        }
                        this.setState({
                            img2: this.state.img2,
                            text2: this.state.text2,
                            idrank: this.state.idrank,
                        })
                    })
                }, 0)
            }
        }
    }
    getmsgdom = (e) => {
        console.dir(e.target.attributes["idkey"].value);
        console.log(this.state.idrank);
        history.push(`/detail?detailkey=${e.target.attributes["idkey"].value}`);
    }

    //-------------------------------------------------------------------------------------------
    render() {
        const {
            img2,
            text2,
            paramsg,
            idrank
        } = this.state;

        const PlaceHolder1 = ({ className = 'placeholder1' }) => (
            <div onClick={() => this.getchildparams("送女友")} className={`${className} placeholder`}>送女友</div>
        );
        const PlaceHolder2 = ({ className = 'placeholder2' }) => (
            <div onClick={() => this.getchildparams("送男友")} className={`${className} placeholder`}>送男友</div>
        );
        const PlaceHolder3 = ({ className = 'placeholder3' }) => (
            <div onClick={() => this.getchildparams("送朋友")} className={`${className} placeholder`}>送朋友</div>
        );
        const PlaceHolder4 = ({ className = 'placeholder4' }) => (
            <div onClick={() => this.getchildparams("送长辈")} className={`${className} placeholder`}>送长辈</div>
        );
        const PlaceHolder5 = ({ className = 'placeholder5' }) => (
            <div onClick={this.gotochoice} className={`${className} placeholder`} style={{ color: '#ff734c' }}>
                {paramsg}
                <i className='iconfont icon-jiantou2' style={{ fontSize: '12px' }}></i>
            </div>
        );

        return (
            <div style={{ background: "white" }}>
                <div className="shadowchoice" style={{ display: 'none', position: 'fixed', background: '#fff', width: '100%', height: "100%", zIndex: 100 }}>
                    <Shadowchoice qwe="123" getchildparams={(res) => this.getchildparams(res)}></Shadowchoice>
                </div>
                <NavBar
                    style={{ background: '#fff' }}
                    mode="light"
                    icon={<i key={1} className={"iconfont icon-fanhui"} style={{ color: 'black', fontSize: 20 }}></i>}
                    onLeftClick={() => { history.go(-1) }}
                    rightContent={
                        [
                            <i key={2} className={"iconfont icon-kefu"} style={{ color: 'black', fontSize: 20, marginRight: '18px' }}></i>,
                            <Pop key={0}></Pop>
                        ]
                    }
                >
                    <span style={{ fontSize: 16 }}>
                        爱情鲜花
                    </span>
                </NavBar>
                <div className='boxdiv' style={{ borderBottom: '2px solid #e9ecf0', background: 'white', display: "flex", justifyContent: "space-around", alignItems: 'center', textAlign: 'center' }}>
                    <div className='choiceone' style={{ width: '25%', lineHeight: '30px', padding: '10px 0', color: '#ff734c' }}>
                        综合
                    </div>
                    <div className='choiceone' style={{ width: '25%', lineHeight: '30px', margin: '10px 0' }}>
                        销量
                    </div>
                    <div className='choiceone' style={{ width: '25%', lineHeight: '30px', margin: '10px 0' }}>
                        价格
                    </div>
                    <div className='choiceone' style={{ width: '25%', lineHeight: '30px', margin: '10px 0' }}>
                        新品
                    </div>
                </div>
                <WhiteSpace size="lg" />
                <div className='placeholderdiv' style={{ padding: "0 15px", background: 'white', fontSize: '12px' }}>
                    <Flex >
                        <Flex.Item><PlaceHolder1 /></Flex.Item>
                        <Flex.Item><PlaceHolder2 /></Flex.Item>
                        <Flex.Item><PlaceHolder3 /></Flex.Item>
                        <Flex.Item><PlaceHolder4 /></Flex.Item>
                        <Flex.Item><PlaceHolder5 /></Flex.Item>
                    </Flex>
                </div>
                <WhiteSpace size="lg" />
                <div style={{ padding: '10px' }}>
                    <div style={{ display: 'flex', justifyContent: 'space-around', flexWrap: 'wrap' }}>
                        {
                            img2.map((m, i) => {
                                return (
                                    <div onClick={(e) => this.getmsgdom(e)} className="rollIn" key={i} style={{ background: 'white', width: '48%', boxShadow: '1px 2px 3px 4px #f0f2f4 ', marginBottom: '10px' }}>
                                        <div idkey={idrank[i]}>
                                            <img style={{ width: '100%' }} src={m} idkey={idrank[i]} />
                                        </div>
                                        <div idkey={idrank[i]} style={{ padding: '0 10px', position: 'relative' }}>
                                            <p idkey={idrank[i]} style={{ lineHeight: '40px', width: '90%', whiteSpace: 'nowrap', textOverflow: 'ellipsis', overflow: 'hidden' }}>
                                                {text2[i][0]}
                                            </p>
                                            <p idkey={idrank[i]} style={{ color: 'red', fontSize: '18px' }}>
                                                {text2[i][1]}
                                            </p>
                                            <p idkey={idrank[i]} style={{ fontSize: '14px', marginBottom: '16px' }}>
                                                {`已售出${text2[i][1].slice("1") * 20.5.toFixed(0)}件`}
                                            </p>
                                            <i idkey={idrank[i]} className='iconfont icon-gouwuche' style={{ fontSize: 24, position: 'absolute', right: 10, top: '70%' }}> </i>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        )
    }
}


//----------------------------------------------------------------------------------------
class Pop extends Component {
    constructor() {
        super();
        this.state = {
            visible: false,
            selected: '',
        };
    }
    onSelect = (opt) => {
        this.setState({
            visible: false,
            selected: opt.props.value,
        });
        console.log(opt.key);
        if (opt.key == 4) {
            history.push('/index/home');
        } else if (opt.key == 5) {
            history.push('/index/classify');
        } else if (opt.key == 6) {
            history.push('/index/mine');
        } else if (opt.key == 7) {
            history.push('/index/cart');
        }
    };
    handleVisibleChange = (visible) => {
        this.setState({
            visible,
        });
    };
    render() {
        return (
            <div>
                <Popover mask
                    overlayClassName="fortest"
                    overlayStyle={{ color: 'currentColor' }}
                    visible={this.state.visible}
                    overlay={[
                        (<Item key='4'><i className='iconfont icon-shouye1-copy'>
                            <span style={{ marginLeft: '10px' }}>首页</span>
                        </i></Item>),
                        (<Item key='5'><i className='iconfont icon-icon04'>
                            <span style={{ marginLeft: '10px' }}>分类</span>
                        </i></Item>),
                        (<Item key='6'><i className='iconfont icon-gouwuche2'>
                            <span style={{ marginLeft: '10px' }}>购物车</span>
                        </i></Item>),
                        (<Item key='7'><i className='iconfont icon-xiaolian'>
                            <span style={{ marginLeft: '10px' }}>我的</span>
                        </i></Item>),
                    ]}
                    align={{
                        overflow: { adjustY: 0, adjustX: 0 },
                        offset: [-10, 0],
                    }}
                    onVisibleChange={this.handleVisibleChange}
                    onSelect={this.onSelect}
                >
                    <div style={{
                        height: '100%',
                        padding: '0 15px',
                        marginRight: '-15px',
                        display: 'flex',
                        alignItems: 'center',
                    }}
                    >

                        <i key={3} className={"iconfont icon-menu"} style={{ color: 'black', fontSize: 20 }}></i>
                    </div>
                </Popover>
            </div>
        )
    }
}